(function() {
  'use strict';

  angular
    .module('wmGitHubUib')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('construction', {
        url: '/in-progress',
        templateUrl: 'app/construction/construction.html'
      })
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/in-progress');
  }

})();

